@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&family=Poppins:ital,wght@0,300;1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.p-landWrap {
  background: url(/react-typing-app/static/media/pexels-sathesh.462a4d04.jpg) no-repeat fixed center;
  background-size: cover;
  height: 100vh;
  min-height: 300px;
  min-width: 300px;
}

.p-landing {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.p-landWrap .p-landing h1 {
  font-family: "New Tegomin", serif;
}

.p-landing > h1 {
  padding: 20px;
  color: aquamarine;
}

.p-landing > button {
  border-radius: 20px;
  width: 6em;
}

.p-landWrap button {
  color: aquamarine;
  border-color: aquamarine;
  font-family: "Poppins", sans-serif;
}

.p-landWrap .btn-outline-success:hover {
  color: black;
  background-color: aquamarine;
  border-color: aquamarine;
}

.p-landWrap .btn-outline-success:not(:disabled):not(.disabled):active {
  color: black;
  background-color: turquoise;
  border-color: turquoise;
}

.p-landGetStarted {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.p-landGetStarted > h1 {
  font-family: "New Tegomin", serif;

  color: tomato;
  font-weight: 700;
}

.p-landGetStarted > button {
  width: 15em;
  /* margin: auto; */
}

.p-landWrap .justify-content-md-center {
  margin-top: 100px;
}

